
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'ImageContainer',
  props: {
    height: {
      type: String,
      required: true,
    },
    width: {
      type: String,
      required: true,
    },
    imgUrl: {
      type: String,
      required: true,
    },
    name: {
      type: String,
      required: true,
    },
  },
  setup() {
    const hasHttpInUrl = (imgUrl: string): boolean => {
      // it is emulate of checking image exist
      return imgUrl.match(/(http(s?))/gim) != null
    }
    return {
      hasHttpInUrl,
    }
  },
})
